'use client'
import { useState, useEffect } from 'react';

export default function DetailsClient({ type, id, data, recommendations, castData, providers, reviews }) {
  const [userRating, setUserRating] = useState(5); // Puntuación inicial vacía
  const [ratingSubmitted, setRatingSubmitted] = useState(false);
  
  const [recommendationsList, setRecommendationsList] = useState([]);
  
  useEffect(() => {
    if (recommendations) {
      setRecommendationsList(recommendations.results);
    }
  }, [recommendations]);

  const handleStarClick = (value) => {
    setUserRating(value);
  };

  const submitReview = async () => {
    alert(`Puntuación enviada exitosamente: ${userRating}/10`);
    setRatingSubmitted(true); // Cambiar a modo de anillo
  };

  return (
    <div className="movie-details">
      <h1>{data.title}</h1>
      <div className="movie-meta">
        <p>Estreno: {data.release_date}</p>
        <p>Duración: {data.runtime} min</p>
        <p>Idioma: {data.original_language}</p>
      </div>

      <div>
        <h3>Críticas de usuarios</h3>
        {reviews?.results?.map((review, index) => (
          <div key={index}>
            <p>{review.content}</p>
          </div>
        ))}
      </div>

      <div>
        <h3>Recomendaciones</h3>
        {recommendationsList.map((rec, index) => (
          <div key={index}>
            <p>{rec.title || rec.name}</p>
          </div>
        ))}
      </div>

      <div>
        <button onClick={submitReview}>Enviar Puntuación</button>
      </div>
    </div>
  );
}
